<template>
  <div class="content">
      <div class="scrollWrapper" ref="wrapper">
          <div v-show="flag.homeFlag">
              <div class="header">L&nbsp; E&nbsp; P&nbsp; R&nbsp; I&nbsp; N&nbsp; T</div>
              <mt-swipe :auto="4000" class="swipe">
                  <mt-swipe-item v-for="item in banners" :key="item.id">
                      <img :src="'http://sylius.papamk.com/'+item.image" alt="" class="swipe-item">
                  </mt-swipe-item>
              </mt-swipe>
              <ul class="taxons">
                  <li @click="toHardCover">
                      <img src="http://sylius.papamk.com/media/image/55/5d/471cbaa6663ae4be65a69fe5857d.png">
                  </li>
                  <li @click="toPremiumHardCover">
                      <img src="http://sylius.papamk.com/media/image/2f/19/920058d022dcfed1ac7de3ebf149.png">
                  </li>
                  <li @click="toSoftCover">
                      <img src="http://sylius.papamk.com/media/image/01/02/6881dbbfbe1bb557dd4414117466.png">
                  </li>
              </ul>
          </div>
      </div>
      <hard-cover v-show="flag.hardCoverFlag" :taxons="taxons1"></hard-cover>
      <premiumHard-cover v-show="flag.premiumHardCoverFlag"></premiumHard-cover>
      <soft-cover v-show="flag.softCover"></soft-cover>
  </div>
</template>

<script>
import router from '../router/index'
import hardCover from './hardCover.vue'
import premiumHardCover from './premiumHardCover.vue'
import softCover from './softCover.vue'
import bus from './bus'
import BScroll from 'better-scroll'
export default{
    router,
    components:{
    	hardCover,
        premiumHardCover,
        softCover
    },
    data(){
        return {
            selected:'',
            flag:{
                homeFlag:true,
                hardCoverFlag:false,
                premiumHardCoverFlag:false,
                softCoverFlag:false,
            },
            banners:[],
            taxons:[],
            taxons1:[],
        }
    },
    props:[
    	'flags'
    ],
    methods:{
        toHardCover(){
        	this.flag.homeFlag=false;
        	this.flag.hardCoverFlag=true;
        	bus.$emit('sendtaxons',this.taxons[0]);
        	this.$emit('update:flags',false);
        	router.push({path:"/home/hardcover-taxons8x8"})
        },
        toPremiumHardCover(){
            this.flag.homeFlag=false;
            this.flag.premiumHardCoverFlag=true;
        },
        toSoftCover(){
            this.flag.homeFlag=false;
            this.flag.softCoverFlag=true;
        }
    },
    created(){
    	bus.$on('returnHome',()=>{
    		this.$emit('update:flags',true);
    		this.flag.homeFlag=true;
    		this.flag.hardCoverFlag=false;
        });
        this.$http.get('http://sylius.papamk.com/app_dev.php/api/banners/').then(resp =>{
            this.banners=resp.body;
        });
        this.$http.get('http://sylius.papamk.com/app_dev.php/api/v1/taxons/album').then(resp =>{
            this.taxons=resp.body.children;
            this.taxons1=resp.body.children[0].children;
//            console.log(this.taxons[0].images[0].path);
//            this.img1=resp.body.children[0].images[0].path;
//            this.img2=resp.body.children[1].images[0].path;
//            this.img3=resp.body.children[2].images[0].path;
        });
    },
    mounted(){
//    	this.$nextTick(() =>{
//    		this.scroll=new BScroll(this.$refs.wrapper)
//        })
    }
}
</script>

<style lang="less" scoped>
  body{
    width: 100%;
  }
  .content{
    /*display: flex;*/
    width: 100%;
    height: 100%;
    background-color: #eeeeee;
  }
  .header{
    z-index:666;
    text-align: center;
    line-height: 50px;
    font-size: 20px;
    color: #999;
    width: 100%;
    height: 50px;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom:1px solid #7e8c8d;
  }
  .swipe{
    margin-top:51px;
    height: 200px;
    width:100%;
  }
  .swipe-item{
    height: 200px;
    width: 100%;
  }
  .taxons{
    margin-bottom: 51px;
    margin-top: 5px;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height:auto;
    li{
      width: 45%;
      margin:5px 0 5px 3.33%;
      img{
        display: inline-block;
        width: 100%;
          height: 100%;
      }
    }
  }

</style>



