<template>
    <div>
        <div class="account_wrapper" v-show="display_account">
            <mt-header fixed title="Me" class="account">
                <mt-button icon="more" slot="right" class="account-edit" @click="toPersonal"></mt-button>
            </mt-header>
            <div class="content">
                <div class="header">
                    <span class="avatar">avatar</span>
                    <span class="login" @click="display($event)">{{message}}</span>
                </div>
                <div class="body">
                    <div class="alarm_wrapper">
                        <div class="alarm"><img src="../../static/img/alarm.svg"></div>
                        <div class="text">
                            <div>Create your items,select</div>
                            <div>your pictures and customize them</div>
                        </div>
                    </div>
                    <div class="toHome_wrapper">
                        <div class="toHome" @click="toHome">
                            <img src="../../static/img/alarm.svg">CREATE
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <login v-show="display_login"></login>
        <personal v-show="display_personal"></personal>
    </div>
</template>

<script>
    import login from './account/login.vue'
    import personal from './account/personal.vue'
    import bus from './bus'
    import router from '../router/index'
    export default{
        data(){
            return {
            	message:'Login',
                display_account:true,
                display_login:false,
                display_personal:false
            }
        },
        props:[
        	'flags'
        ],
        methods:{
            display(e){
            	if(e.target.innerText==="Login"){
                    this.display_account=false;
                    this.display_login=true;
                    this.display_personal=false;
                    this.$emit('update:flags',false);
                }
            },
            toPersonal(){
                this.display_account=false;
                this.display_login=false;
                this.display_personal=true;
                this.$emit('update:flags',false);
            },
            toHome(){
            	router.push({path:'home'})
            }
        },
        components:{
            login,
            personal
        },
        mounted(){
        	bus.$on('toAccount',msg=>{
        		this.message=msg;
                this.display_account=true;
                this.display_login=false;
                this.display_personal=false;
                this.$emit('update:flags',true);
            });
            bus.$on('toLogin',()=>{
                this.display_account=false;
                this.display_login=true;
                this.display_personal=false;
            });
//            let no=Math.floor(Math.random()*1000);
//            this.$http.post('http://sylius.papamk.com/app_dev.php/api/register',{
//                "email": "m11r4"+no+"yyt25@zzz.com",
//                "firstName": "WENCAN",
//                "lastName": "LIU",
//                "phoneNumber": "17602343425",
//                "subscribedToNewsletter": true,
//                "gender": "u",
//                "password": "123456"
//            }).then(resp =>{
//                console.log('resp',resp)
//            });
        },
        updated(){
        	if(this.message!=='Login'){
        		console.log('updated')
            }
        }

    }
</script>

<style lang="less" scoped>
  .account{
    text-align: center;
    line-height: 50px;
    font-size: 20px;
    color: #999;
    width: 100%;
    height: 50px;
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    border-bottom:1px solid #7e8c8d;
    .account-edit{
      color: #cccccc;
    }
  }
  .content{
      color: #ffffff;
      margin-top: 50px;
      height: auto;
      .header{
          height: 150px;
          background-color: #aaa;
          position: relative;
          .avatar{
              position: absolute;
              top: 110px;
              left: 20px;
              display: block;
              height: 80px;
              width: 80px;
              border: 1px solid #fff;
              border-radius: 50%;
              line-height: 80px;
              text-align: center;
              background-color: skyblue;
          }
          .login{
              position: absolute;
              top: 125px;
              right: 20px;
              /*border: 1px solid #fff;*/
              text-decoration: underline;
              text-align: center;
          }
      }
      .body{
          /*background-color: red;*/
          height: 400px;
          color: #666;
          font-size: 20px;
          text-align: center;
          .alarm_wrapper{
              margin-top: 50px;
              .alarm{
                  img{
                      width: 100px;
                  }
              }
          }
          .toHome_wrapper{
              margin-top: 50px;
              height: 100px;
              .toHome{
                  background-color: #eee;
                  width: 75%;
                  height: 60px;
                  margin: 0 auto;
                  /*background-color: red;*/
                  border: 1px solid #666666;
                  line-height: 60px;
                  img{
                      width: 30px;
                  }
              }
          }
      }
  }
</style>



