<template>
    <div class="orders_wrapper">
        <div class="orders_header">
            <div class="backAccount" @click="toPersonal"><span>&lt;</span>返回</div>
            Orders
        </div>
        <div v-show="displayNoOrder" class="noOrder">
            You don't have any orders
        </div>
        <ul v-show="displayOrder" class="order">
            <li v-for="item in data" :key="item.id">
                订单号：{{item.number}}<br>
                下单日期：{{item.checkoutCompletedAt}}<br>
                总价：{{item.total}}<br>
                付款方式：{{item.payments[0].method.code}}
            </li>
        </ul>
    </div>
</template>

<script>
    import bus from '../../bus'
    import { Indicator } from 'mint-ui'
export default{
	data(){
		return {
            displayNoOrder:false,
            displayOrder:false,
            data:[]
        }
    },
    methods:{
		toPersonal(){
			bus.$emit('toPersonal')
        }
    },
    mounted(){
		bus.$on('displayOrder',data =>{
			Indicator.close();
			console.log(typeof data);
			if(data.length>0){
                this.displayOrder=true;
                this.data=data;
            }else{
				this.displayNoOrder=true;
            }

        });
		bus.$on('toWait',()=>{
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
        })
    }
}
</script>

<style lang="less" scoped>
    .orders_wrapper{
        color:#999;
        .orders_header{
            position: fixed;
            width: 100%;
            top: 0;
            left: 0;
            height: 50px;
            line-height: 50px;
            font-size: 20px;
            background-color: #fff;
            text-align: center;
            border-bottom: 1px solid #7e8c8d;
            .backAccount{
                float: left;
                height: 50px;
                font-size: 20px;
                span{
                    line-height: 50px;
                }
            }
        }
        .noOrder{
            text-align: center;
            margin-top: 60%;
        }
        .order{
            margin-top: 51px;
            li{
                margin: 5px 0;
                border: 1px solid #666666;
            }
        }
    }

</style>



