<template>
    <div class="address_wrapper">
        <div class="address_header">
            <div class="backAccount" @click="toPersonal"><span>&lt;</span>返回</div>
        </div>
        <div class="noAddress" v-show="displayNoAddress">
            <img src="../../../../static/img/express.svg">
            <p>Click here to add the address</p>
        </div>
        <div class="address" v-show="displayAddress">
            <ul>
                <li v-for="item in address" :key="item.id">
                    名字：{{item.firstName}}<br>
                    姓氏: {{item.lastName}}<br>
                    手机号码： {{item.phoneNumber}}<br>
                    省份： {{item.provinceName}}<br>
                    街道： {{item.street}}<br>
                    城市： {{item.city}}<br>
                    邮箱： {{item.email}}
                </li>
            </ul>
        </div>
        <div class="addAddress" @click="toAddAddress">Add Address</div>
    </div>
</template>

<script>
    import bus from '../../bus'
    import { Indicator } from 'mint-ui'
export default{
	data(){
		return {
			displayNoAddress:false,
            displayAddress:false,
            address:[]
        }
    },
    methods:{
		toPersonal(){
			bus.$emit('toPersonal')
        },
        toAddAddress(){

        }
    },
    mounted(){
		bus.$on('displayAddress',data =>{
			Indicator.close();
            if(data.length>0){
            	this.displayAddress=true;
            	this.address=data;
            }else{
            	this.displayAddress=false;
            }
        });
		bus.$on('addressLoading',() =>{
            Indicator.open({
                text: '加载中...',
                spinnerType: 'snake'
            });
        })
    }
}
</script>

<style lang="less" scoped>
    .address_wrapper{
        color: #999;
        .address_header{
            position: fixed;
            width: 100%;
            top: 0;
            left: 0;
            height: 50px;
            line-height: 50px;
            font-size: 20px;
            background-color: #fff;
            text-align: center;
            border-bottom: 1px solid #7e8c8d;
            .backAccount{
                float: left;
                height: 50px;
                font-size: 20px;
                span{
                    line-height: 50px;
                }
            }
        }
        .noAddress{
            margin-top: 60%;
            text-align: center;
            img{
                width: 100px;
                margin-bottom: 20px;
            }
        }
        .address{
            margin-bottom: 60px;
            margin-top: 51px;
            li{
                margin: 5px 0;
                border: 1px solid #666666;
            }
        }
        .addAddress{
            text-align: center;
            line-height: 60px;
            font-size: 25px;
            width: 100%;
            color: #eeeeee;
            background-color: grey;
            position: fixed;
            bottom:0;
            left: 0;
            height: 60px;
        }
    }
</style>



