<template>
    <div class="material_wrapper">
        <div v-show="displayMaterial">
            <div class="taxons1">
                <mt-header fixed title="M a t e r i a l" class="taxons1Header">
                    <mt-button icon="back" slot="left" @click="returnTaxons">返回</mt-button>
                </mt-header>
            </div>
            <ul class="material">
                <li @click="toSelectPicture">
                    <router-link to="/home/hardcover-taxons8x8/weddingDress">
                        <img :src="'http://sylius.papamk.com/media/image/'+taxons.children[0].images[0].path" alt="">
                    </router-link>
                </li>
                <li @click="toSelectPicture">
                    <router-link to="/home/hardcover-taxons8x8/babyBook">
                        <img :src="'http://sylius.papamk.com/media/image/'+taxons.children[1].images[0].path" alt="">
                    </router-link>
                </li>
                <li @click="toSelectPicture">
                    <router-link to="/home/hardcover-taxons8x8/family">
                        <img :src="'http://sylius.papamk.com/media/image/'+taxons.children[2].images[0].path" alt="">
                    </router-link>
                </li>
                <li @click="toSelectPicture">
                    <router-link to="/home/hardcover-taxons8x8/loveStory">
                        <img :src="'http://sylius.papamk.com/media/image/'+taxons.children[3].images[0].path" alt="">
                    </router-link>
                </li>
            </ul>
        </div>
        <router-view v-show="displayView"></router-view>
    </div>
</template>

<script>
    import bus from '../../bus'
export default{
    data(){
        return {
            displayMaterial:true,
            displayView:true
        }
    },
    methods:{
        returnTaxons(){
        	bus.$emit('returnTaxons')
        },
        toSelectPicture(){
        	this.displayMaterial=false;
        	this.displayView=true;
        }
    },
    computed:{

    },
    props:['taxons'],
    mounted(){
        bus.$on('returnMaterial',()=>{
        	this.displayMaterial=true;
        	this.displayView=false;
        })
    }
}
</script>

<style lang="less" scoped>

.material{
    margin-top: 51px;
    li{
      display: block;
      width: 100%;
      img{
        display: block;
        width: 100%;
      }
    }
}
.taxons1Header{
    font-size: 20px;
    background-color: #fff;
    color: #999;
    height: 50px;
    border-bottom: 1px solid #eeeeee;
}

</style>



