import Vue from 'vue'
import Router from 'vue-router'
import home from '../components/home.vue'
import cart from '../components/cart.vue'
import account from '../components/account.vue'
import hardCoverTaxons8x8 from '../components/taxons/hardCoverTaxons8x8.vue'
import hardCoverTaxons8x10 from '../components/taxons/hardCoverTaxons8x10.vue'
import hardCoverTaxons10x10 from '../components/taxons/hardCoverTaxons10x10.vue'
import wenddingdress from '../components/taxons/taxons1/taxons2/weddingDress.vue'
import family from '../components/taxons/taxons1/taxons2/family.vue'
import loveStory from '../components/taxons/taxons1/taxons2/loveStory.vue'
import babyBook from '../components/taxons/taxons1/taxons2/babybook.vue'
Vue.use(Router);

export default new Router({
    routes: [
        {
            path:'/home',
            component:home,
            children:[
                {
                    path:'hardcover-taxons8x8',
                    component:hardCoverTaxons8x8,
                    children:[
                        {
                            path:'weddingDress',
                            component:wenddingdress
                        },
                        {
                            path:'loveStory',
                            component:loveStory
                        },
                        {
                            path:'family',
                            component:family
                        },
                        {
                            path:'babyBook',
                            component:babyBook
                        }
                    ]
                },
                {
                    path:'hardcover-taxons8x10',
                    component:hardCoverTaxons8x10,
                },
                {
                    path:'hardcover-taxons10x10',
                    component:hardCoverTaxons10x10,
                }
            ]
        },
        {
            path:'/cart',
            component:cart
        },
        {
            path:'/account',
            component:account
        },
        {
          path:'/*',
          redirect:'/home'
        }
    ],
    linkActiveClass:'active'
})



