<template>
    <div class="login_wrapper">
        <div class="close"><span @click="closeToAccount">X</span></div>
        <div class="container">
            <div class="login_header">
                <div class="signup" @click="tab($event)">Sign Up</div>
                <div class="login" @click="tab">Log In</div>
            </div>
            <div class="form">
                <mt-field label="邮箱" placeholder="E-mail" type="email" v-model="email"></mt-field>
                <mt-field label="密码" placeholder="Password" type="password" v-model="password"></mt-field>
            </div>
            <!--<div class="message">{{message}}</div>-->
            <div class="toSignUpOrLogin">
                <div v-show="displaySignUp" class="toSignUp" @click="toAccount($event)">SIGN UP</div>
                <div v-show="displayLogin" class="toLogin" @click="toAccount($event)">LOG IN</div>
            </div>
            <div class="forgotpwd">Forgot password ?</div>
            <div class="facebook">facebook</div>
        </div>
    </div>
</template>

<script>
    import bus from '../bus'
    import { Toast } from 'mint-ui';
    import { Indicator } from 'mint-ui';
    export default{
        data(){
            return {
                displayLogin:false,
                displaySignUp:true,
            	email:'',
                password:'',
                message:''
            }
        },
        methods:{
        	closeToAccount(){
                bus.$emit('toAccount','Login');
            },
            tab(e){
//        		console.log(e.target.innerText);
        		if((this.displayLogin===false&&e.target.innerText==='Log In')||(this.displaySignUp===false&&e.target.innerText==='Sign Up')){
                    e.target.parentNode.children[0].style.backgroundColor='#87ceeb';
                    e.target.parentNode.children[1].style.backgroundColor='#87ceeb';
        			e.target.style.backgroundColor='#999';
                    this.displaySignUp=!this.displaySignUp;
                    this.displayLogin=!this.displayLogin;
                }
            },
            toAccount(e){
//            	console.log(e.target.innerText);
                let email=this.email;
                let psw=this.password;
                let regExp=/^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g;
                if(!regExp.test(email)){
//                	this.message='邮箱不正确，请重新输入!';
                    Toast({
                        message: '请输入正确邮箱',
                        position:'top'
                    });
                    return false;
                }
            	if(e.target.innerText==='SIGN UP'){
                    Indicator.open({
                        text: '注册中...',
                        spinnerType: 'snake'
                    });
                    this.$http.post('http://sylius.papamk.com/app_dev.php/api/register',{
                        "email": email,
                        "password": psw,
                        "gender":'u'
                    }).then(resp =>{
                        Indicator.close();
                        console.log(resp);
                        Toast({
                            message: '注册成功！',
                            position:'top'
                        });
                        sessionStorage.setItem('access_token',resp.body.access_token);
                        setTimeout(function () {
                            bus.$emit('toAccount',email);
                        },3000)
                    }).catch(resp =>{
                    	//this.message='注册失败，请重新输入！'
                        Indicator.close();
                        Toast({
                            message: '注册失败！',
                            position:'top'
                        });
                    })
                }else{
                    Indicator.open({
                        text: '登录中...',
                        spinnerType: 'snake'
                    });
                    this.$http.get(`http://sylius.papamk.com/app_dev.php/api/oauth/v2/token?client_id=demo_client&client_secret=secret_demo_client&grant_type=password&username=${email}&password=${psw}`,).then(resp =>{
                    	Indicator.close();
                        console.log(resp);
                        Toast({
                            message: '登录成功！',
                            position:'top'
                        });
                        sessionStorage.setItem('access_token',resp.body.access_token);
                        setTimeout(function () {
                            bus.$emit('toAccount',email);
                        },3000);
                        //console.log(sessionStorage.getItem('token'))
                    }).catch(resp =>{
                    	Indicator.close();
                        //this.message='登录失败，请重新输入！'
                        Toast({
                            message: '登录失败！',
                            position:'top'
                        });
                    });
                }
                this.email='';
                this.password='';
                this.message='';
            }
        }

    }
</script>

<style lang="less" scoped>
    .login_wrapper{
        .close{
            margin-top: 20px;
            span{
                color: #aaaaaa;
                font-size: 25px;
                margin-left: 20px;
            }
        }
        .container{
            width: 75%;
            text-align: center;
            margin: 80px auto;
            .login_header{
                display: flex;
                background-color: skyblue;
                line-height: 50px;
                color: #ffffff;
                height: 50px;
                .signup{
                    background-color: #999;
                    flex: 1;
                }
                .login{
                    flex: 1;
                }
            }
            .form{
                color: #666;
                margin-top: 20px;
            }
            /*.message{*/
                /*margin-top: 10px;*/
                /*font-size:20px;*/
                /*height: 20px;*/
                /*color: red;*/
                /*text-align: center;*/
            /*}*/
            .toSignUpOrLogin{
                color: #ffffff;
                margin-top: 35px;
                text-align: center;
                line-height: 50px;
                height: 50px;
                background-color: #999;
            }
            .forgotpwd{
                color: #666;
                text-align: center;
                margin-top: 10px;
            }
            .facebook{
                color: #ffffff;
                margin-top: 50px;
                height: 50px;
                line-height: 50px;
                text-align: center;
                background-color: blueviolet;
            }
        }
    }
</style>



