<template>
  <div id="app">
    <!--<transition enter-active-class="animated fadeInLeft" leave-active-class="animated fadeOutRight">-->
      <keep-alive>
        <router-view :flags.sync="flag"></router-view>
      </keep-alive>
    <!--</transition>-->

    <div class="tab" v-show="flag">
      <div class="tab-item">
        <router-link to="/home">
          <img src="../static/img/home.svg"/>
        </router-link>
      </div>
      <div class="tab-item">
        <router-link to="/cart">
          <img src="../static/img/cart.svg"/>
        </router-link>
      </div>
      <div class="tab-item">
        <router-link to="/account">
          <img src="../static/img/account-copy.svg"/>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name: 'app',
    data(){
      return {
          flag:true
      }
    }
}
</script>

<style lang="less" scoped>
  body{
    height: 100%;
  }
  #app{
    height: 100%;
  }
  .tab{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    width: 100%;
    height: 50px;
    line-height: 50px;
    background-color: #fff;
    border-top:1px solid #7e8c8d;
    .tab-item{
      flex:1;
      text-align: center;
      a{
        display: block;
        padding-top:10px;
        img{
          height: 30px;
        }
      }
      a.active{
        background-color: #ddd;
      }
    }
  }
</style>



