<template>
    <div >
        <div class="inch-wrapper" v-show="displayHardCoverTaxons8x8">
            <ul class="item8x8">
                <li v-for="item in taxons[0].images" @click="toMaterial">
                    <img :src="'http://sylius.papamk.com/media/image/'+item.path">
                </li>
            </ul>
        </div>
        <material v-show="displayMaterial" :taxons="taxons[0]"></material>
    </div>

</template>

<script>
    import router from '../../router/index'
    import material from './taxons1/material.vue'
    import bus from '../bus'
    export default{
        data(){
            return {
                displayMaterial:false,
                displayHardCoverTaxons8x8:true
            }
        },
        props:['taxons'],
        computed:{

        },
        methods:{
            toMaterial(){
                this.displayMaterial=true;
                this.displayHardCoverTaxons8x8=false;
                bus.$emit('toMaterial')
            }
        },
        components:{
        	material,
        },
        mounted(){
        	bus.$on('returnTaxons',()=>{
        		this.displayMaterial=false;
        		this.displayHardCoverTaxons8x8=true;
            })
        }
    }
</script>

<style lang="less" scoped>
  .item8x8{
     margin-top: 76px;
     li{
       display: block;
       width: 100%;
       img{
           margin: 0 auto;
         display: block;
         width: 80%;
       }
     }
   }
</style>




