<template>
    <div class="personal_wrapper">
        <div v-show="display.displayPersonal">
            <div class="personal_header">
                <div class="backAccount" @click="toAccount"><span>&lt;</span>返回</div>
                Set up
            </div>
            <div class="personal">
                <div @click="toLogout"><img src="../../../static/img/account-copy.svg" alt=""><p>{{message}}</p></div>
                <div @click="toOrder"><img src="../../../static/img/manage-order.svg" alt=""><p>Orders</p></div>
                <div @click="toLogin"><img src="../../../static/img/bags.svg" alt=""><p>Package</p></div>
                <div @click="toLogin"><img src="../../../static/img/star-empty.svg" alt=""><p>Design</p></div>
                <div @click="toAddress"><img src="../../../static/img/wxb定位.svg" alt=""><p>Address</p></div>
                <div @click="toLogin"><img src="../../../static/img/news.svg" alt=""><p>News</p></div>
                <div @click="toLogin"><img src="../../../static/img/password.svg" alt=""><p>Password</p></div>
            </div>
        </div>
        <orders v-show="display.displayOrders"></orders>
        <_Address v-show="display.displayAddress"></_Address>
    </div>
</template>

<script>
    import orders from './personalTaxons/orders.vue'
    import _Address from './personalTaxons/address.vue'
    import bus from '../bus'
    import { MessageBox } from 'mint-ui';
    export default{
        data(){
        	return {
                message:'Login',
                display:{
                	displayPersonal:true,
                    displayOrders:false,
                    displayAddress:false
                }
            }
        },
        components:{
        	orders,
            _Address
        },
        methods:{
        	toAccount(){
        		if(this.message!=='Login'){
        			bus.$emit('toAccount',this.message);
                }else{
                    bus.$emit('toAccount','Login');
                }

            },
            toLogin(){
        		if(this.message!=='Login'){

                }else{
                    bus.$emit('toLogin')
                }
            },
            toOrder(){
//            	if(this.message!=='Login'){
                    //sessionStorage.clear();
            		this.display.displayPersonal=false;
            		this.display.displayOrders=true;
            		bus.$emit('toWait');
            		//console.log(sessionStorage);
            		this.$http.get("http://sylius.papamk.com/app_dev.php/api/account/orders/",{headers:{'authorization':'Bearer SampleToken','content-type':'application/json'},}).then(resp =>{
                        console.log(resp.body._embedded.items);
//                        if(resp.body.total>0){
                        	bus.$emit('displayOrder',resp.body._embedded.items)
//                        }else{
//                        	bus.$emit('displayOrder')
//                        }
                    })
//                }else{
//            		this.toLogin();
//                }
            },
            toLogout(){
            	if(this.message!=='Login'){
                    MessageBox.confirm('Are you sure to logout？').then(action =>{
                        bus.$emit('toAccount','Login');
                        //this.message='Please Login'
                    }).catch(action =>{
                        return false;
                    })
                }else{
            		this.toLogin();
                }
            },
            toAddress(){
                this.display.displayPersonal=false;
            	this.display.displayAddress=true;
            	bus.$emit('addressLoading');
//            	if(this.message!=='Login'){
            		this.$http.get('http://sylius.papamk.com/app_dev.php/api/address_book/',{headers:{'authorization':'Bearer SampleToken'}}).then(resp =>{
            			console.log(resp);
            			bus.$emit('displayAddress',resp.body);
                    })
//                }else{
//            		this.toLogin();
//                }
            }
        },
        mounted(){
            bus.$on('toAccount',msg =>{
            	this.message=msg;
            });
            bus.$on('toPersonal',()=>{
            	this.display.displayPersonal=true;
                this.display.displayOrders=false;
            	this.display.displayAddress=false;
            })
        }
    }
</script>

<style lang="less" scoped>
    .personal_wrapper{
        color: #999;
        .personal_header{
            position: fixed;
            width: 100%;
            top: 0;
            left: 0;
            height: 50px;
            line-height: 50px;
            font-size: 20px;
            background-color: #fff;
            text-align: center;
            border-bottom: 1px solid #7e8c8d;
            .backAccount{
                float: left;
                height: 50px;
                font-size: 20px;
                span{
                    line-height: 50px;
                }
            }
        }
        .personal{
            width: 90%;
            display: flex;
            flex-wrap: wrap;
            margin: 51px auto;
            div{
                text-align: center;
                margin: 2%;
                width: 46%;
                height: 150px;
                background-color: #eeeeee;
                border-radius: 10px;
                img{
                    display: block;
                    margin: 20% 33.33%;
                    width: 50px;
                }
            }
        }
    }
</style>



