<template>
    <div>
        <div class="header">
            <mt-header fixed title="Select picture" class="taxons1Header">
                <mt-button icon="back" slot="left" @click="returnMaterial">返回</mt-button>
            </mt-header>
        </div>
        <div class="select_picture">
            <ul>
                <li v-for="item in img"><img :src="item" alt=""></li>
            </ul>
            <div class="toSelect" @click="toSelect">点击为定制相册添加图片</div>
            <div class="confirm" @click="confirm">确认上传</div>
            <input type="file" accept="image/gif,image/jpeg,image/jpg,image/png" style="display:none" @change="changeImage($event)" ref="picture">
            <!--<mt-actionsheet-->
                <!--:actions="actions"-->
                <!--v-model="sheetVisible">-->
            <!--</mt-actionsheet>-->
        </div>
    </div>
</template>

<script>
    import bus from '../../../bus'
export default{
    data(){
    	return {
//    		sheetVisible:false,
//            actions:[
//                {name:'拍照',method:function () {
//                    return false;
//                }},
//                {name:'从相册选择',method:function () {
//                    this.$refs.picture.click();
//                }}
//            ]
            img:[]
        }
    },
    methods:{
    	returnMaterial(){
    		bus.$emit('returnMaterial')
        },
        toSelect(){
//    		this.sheetVisible=true;
            this.$refs.picture.click();
        },
        changeImage(e){
            var file = e.target.files[0];
            console.log(file);
            var reader = new FileReader();
            var that = this;
            reader.readAsDataURL(file);
            reader.onload = function() {
                that.img.push(this.result);
                //console.log(that.img);
            };

        },
        confirm() {
            if (this.$refs.picture.files.length !== 0) {
                var image = new FormData();
                image.append('types[]', 'page-1');
                image.append('files[]', this.$refs.picture.files[0]);
                //console.log(image);
                this.$http.post('http://sylius.dev/app_dev.php/api/designs/6/images/',image,{
                    headers: {
                        "authorization": "Bearer SampleToken",
                    }
                }).then(resp =>{
                	console.log(resp);
                }).catch(resp =>{
                	console.log(resp);
                });
            }

        }
    },
}
</script>

<style lang="less" scoped>
    .taxons1Header{
        font-size: 20px;
        background-color: #fff;
        color: #999;
        height: 50px;
        border-bottom: 1px solid #eeeeee;
    }
    .select_picture{
        background-color: white;
        ul{
            margin-top: 51px;
            /*margin-bottom: 102px;*/
            li{
                display:inline-block;
                img{
                    width: 93px;
                    height: 100px;
                }
            }
        }
        form{
            display: none;
        }
        .toSelect{
            /*border-radius: 5px;*/
            position: fixed;
            bottom: 50px;
            left: 0;
            color: #666;
            background-color: #eee;
            text-align: center;
            line-height: 50px;
            font-size: 20px;
            border: 1px solid #dddddd;
            width: 100%;
            height: 50px;
        }
        .confirm{
            position: fixed;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 50px;
            line-height: 50px;
            text-align: center;
            font-size: 20px;
            color: #666;
            background-color: #eeeeee;
            border: 1px solid #dddddd;

        }
    }
</style>



