<template>
    <div>
        <div v-show="displayInch">
            <div class="=taxons1">
                <mt-header fixed title="T y p e" class="taxons1Header">
                    <mt-button icon="back" slot="left" class="backHome" @click="returnHome">返回</mt-button>
                </mt-header>
            </div>
            <mt-tabbar class="inch" v-model="selected">
                <mt-tab-item id="inch1" selected>
                    <router-link to="/home/hardcover-taxons8x8" tag="div">
                        8 X 8inch
                    </router-link>
                </mt-tab-item>
                <mt-tab-item id="inch2">
                    <router-link to="/home/hardcover-taxons8x10" tag="div">
                        8 X 10inch
                    </router-link>
                </mt-tab-item>
                <mt-tab-item id="inch3">
                    <router-link to="/home/hardcover-taxons10x10" tag="div">
                        10 X 10inch
                    </router-link>
                </mt-tab-item>
            </mt-tabbar>
        </div>
        <router-view :taxons="taxons"></router-view>
    </div>
</template>

<script>
    import bus from './bus'
    import router from '../router/index'
export default{
    data(){
    	return {
    		selected: true,
            displayInch:true
        }
    },
    props:['taxons'],
    methods:{
    	returnHome(){
    		bus.$emit('returnHome');
    		router.push({path:'/home'})
        },
    },
    mounted(){
    	bus.$on('toMaterial',()=>{
    		this.displayInch=false;
        });
    	bus.$on('returnTaxons',()=>{
    		this.displayInch=true;
        })
    }
}
</script>

<style lang="less" scoped>
    .taxons1Header{
        font-size: 20px;
        background-color: #fff;
        color: #999;
        height: 50px;
        border-bottom: 1px solid #eeeeee;
    }
    .inch{
        background-color: #fff;
        flex: 1;
        position: fixed;
        width: 100%;
        height: 25px;
        top: 51px;
        left: 0;
    .is-selected{
        background-color: #eee;
    }
    }
    .active{
        /*line-height: 25px;*/
        height: 25px;
        background-color: #eee;
    }
    .mint-tab-item{
        padding: 0;
    }
</style>



